.description {
  box-sizing: border-box;
  position: absolute;
  background: #004b72;
  padding: 10px;
  color: #fff;
  text-align: center;
  border-radius: 5px;
  width: 100%;
  margin-top: 40px;
}
.description:after {
  bottom: 100%;
  left: 50%;
  border: solid transparent;
  content: " ";
  height: 0;
  width: 0;
  position: absolute;
  pointer-events: none;
  border-bottom-color: #004b72;
  border-width: 30px;
  margin-left: -30px;
}
.description .underlined:after {
  margin: 0 auto;
}
.description .title {
  font-family: 'Noto Serif', sans-serif;
  font-size: 16px;
  font-style: italic;
}
