#rating-section {
  width: 80%;
  height: 700px;
  margin: 0 auto;
  background-color: #004b72;
}
#rating-section h1 {
  text-align: center;
  color: #fff;
  padding-top: 35px;
}
#rating-section .chart-container {
  width: 800px;
  height: 800px;
  margin: 0 auto;
  padding-top: 60px;
}
@media (max-width: 1000px) {
  #rating-section .chart-container {
    width: 700px;
    height: 700px;
  }
}
@media (max-width: 850px) {
  #rating-section .chart-container {
    width: 650px;
    height: 650px;
  }
}
@media (max-width: 780px) {
  #rating-section .chart-container {
    width: 550px;
    height: 550px;
  }
}
@media (max-width: 700px) {
  #rating-section .chart-container {
    width: 480px;
    height: 480px;
  }
}
@media (max-width: 550px) {
  #rating-section .chart-container {
    width: 400px;
    height: 400px;
  }
}
@media (max-width: 480px) {
  #rating-section .chart-container {
    width: 340px;
    height: 340px;
  }
}
